import React, { useContext } from 'react'
import { UserfContext } from "../context/userState";
import { Login } from "./Login";

export const Home =()=>{
    const {isToken } = useContext(UserfContext);

       if(isToken)
       return (
        <div>
            Home
        </div>)
     else
       return <Login/>
        
        
    };
